/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import Switch from 'rc-switch';
import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';
import { getCurrentUser } from '../../helpers/Utils';
import StateButton from '../StateButton';

import 'rc-switch/assets/index.css';

const AddPrizeModal = ({ setOpenAddModal, id }) => {
  const [formValues, setFormValues] = useState({
    hackathon_id: id,
    sponsor_prize: 1,
    name: '',
    description: '',
  });

  const { access_token } = getCurrentUser();

  const handleChange = (event, key, value = false) => {
    setFormValues({
      ...formValues,
      [key]: value ? event : event.target.value,
    });
  };

  const handleSubmit = async (newData) => {
    return new Promise((success, failed) => {
      const addPrize = async () => {
        try {
          const response = await instance({
            method: 'POST',
            url: '/addPrizes',
            data: { prizes: { ...newData } },
            headers: {
              Authorization: `Bearer ${access_token}`,
            },
          });

          const { data } = response;
          if (data.success) {
            setFormValues({ ...formValues, ...newData });
            success('Prize added successfully!');
          } else {
            failed('Failed to add Prize!');
          }
        } catch (error) {
          console.log(error);
          failed('Failed to add Prize!');
        }
      };

      addPrize();
    });
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-prize-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.prize" />
          </Label>
          <Input
            type="text"
            value={formValues.name}
            onChange={(event) => handleChange(event, 'name')}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.prize-description" />
          </Label>
          <Input
            type="textarea"
            rows="6"
            value={formValues.description}
            onChange={(event) => handleChange(event, 'description')}
          />
        </FormGroup>

        <FormGroup className="d-flex">
          <Switch
            className="custom-switch custom-switch-primary custom-switch-small mr-2"
            checked={formValues.sponsor_prize}
            onChange={(primary) => handleChange(primary, 'sponsor_prize', true)}
          />
          <Label>
            <IntlMessages id="challenges.modal.is-sponsor-prize" />
          </Label>
        </FormGroup>
      </ModalBody>

      <ModalFooter>
        <StateButton
          id="add-prize-buttom"
          color="secondary"
          onClick={() => handleSubmit(formValues)}
          next={() => window.location.reload()}
        >
          <IntlMessages id="challenges.modal.application-add" />
        </StateButton>

        <Button color="danger" onClick={() => setOpenAddModal(false)}>
          <IntlMessages id="challenges.modal.application-cancel" />
        </Button>
      </ModalFooter>
    </>
  );
};

export default AddPrizeModal;
