/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import { Card, CardBody, CardTitle } from 'reactstrap';
import InputDisplay from '../../containers/application/InputDisplay';
import AppError from '../../helpers/AppError';
import instance from '../../helpers/axiosInstance';
import IntlMessages from '../../helpers/IntlMessages';
import { getCurrentUser } from '../../helpers/Utils';
import { Separator } from '../common/CustomBootstrap';

const PrizesList = ({ id = 1 }) => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [error, setError] = useState('');

  const { access_token } = getCurrentUser();

  const fetchPrizes = async () => {
    try {
      const response = await instance({
        method: 'GET',
        url: `/getPrizes/${id}`,
        headers: {
          Authorization: `Bearer ${access_token}`,
        },
      });

      if (response.data.success) {
        setData(response.data.Prizes);
      } else {
        setError(response.data.message);
      }
      setLoading(false);
    } catch (err) {
      setError(err.message);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPrizes();
  }, []);

  if (error) throw new AppError(error);

  return (
    <Card>
      <CardBody>
        <CardTitle>
          <span className="h3">
            <IntlMessages id="challenges.added-prizes" />
          </span>
        </CardTitle>

        <Separator className="my-2" />

        {loading ? (
          <div className="d-flex justify-content-center m-4">
            <div className="loading" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        ) : (
          <>
            {data.map((item) => (
              <InputDisplay
                key={item.id}
                messageId="challenges.modal.prize"
                name="challenges-prizes"
                value={item.name}
              />
            ))}
          </>
        )}
      </CardBody>
    </Card>
  );
};

export default PrizesList;
